.navbar {
    position: fixed;
    width: 100%;
    opacity: 0.9;
    justify-content: space-around;
    background-color: var(--bgColor1);
    z-index: 10;
    transition: var(--speed1) ease;
}

.navbar-brand {
    font-weight: 10;
    /* height: 100%; */
    color: var(--txtColor1) !important;
}

.navbar-collapse {
    justify-content: flex-end;
}

.navbar-nav {
    align-items: center;
    opacity: 0.6;
}

.very-small{
    font-size: 15px;
    letter-spacing: -0.03em;
    opacity: 0.7;
}

.nav-link{
    color: var(--txtColor2) !important;
}

.mode-switcher {
    width: 150px;
    padding: 20px;
    color: var(--txtColor1);
}

.mode-bg {
    width: 30px;
    height: 20px;
    background-color: var(--txtColor1);
    border-radius: 10px;
    display: flex;
    padding: 1px;
    align-items: center;
}
.mode-button {
    width: 18px;
    height: 18px;
    background-color: var(--bgColor1);
    border-radius: 10px;
    padding: 0;
}

.mode-bg:hover {
    cursor: pointer;
}
.jumbotron {
    /* background-image: url('/public/images/orion.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed; */
    position:relative;
    min-height: 100vh;
    width: 100%;
    padding: 0;
    margin: 0;
    background: none;
    z-index: -1;
}

.jumbotron--container {
    width: 100%; 
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center !important; 
    align-items: center !important;
    opacity: 80% !important;
}


.jumbotron--title--container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* text-align: center; */
    font-weight: 100;
    font-size: 30px; 
}


.jumbotron--title--container > h1 {
    text-align: center;
    font-weight: 100  !important;
    font-size: 80px  !important;
}

.jumbotron--title--container > p{
    text-align: center;
    font-weight: 100  !important;
    font-size: 20px  !important;
}
.footer--link:hover {
    text-decoration: none;
}

.footer {
    height: 100px;
    margin-top: 50px;
    background-color: var(--bgColor1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transition: var(--speed1) ease;
}

.footer > *{
    color: var(--txtColor1) !important;
}


.footer--info {
    width: 70%;
    display: flex;
    justify-content: space-between;
}



.footer--copyright {
    width: 75%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.page--container {
    min-height: 100vh;
    max-width: 800px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    opacity: 0.9;
}

.title {
    padding: 20px;
    text-align: center;
    font-weight: 100 !important;
    font-size: var(--titleSize1);
}

.wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0.1;
}

.particle {
    width: 3px;
    height: 3px;
    border-radius: 3px;
    position: absolute;
    background-color: var(--ptColor);
    box-shadow: 0 0 3px 2px var(--ptColor);
}

.canvas--items {
    position: absolute;
    /* left: 0; */
    top: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.counter {
    text-align: center;
    opacity: 0.7;
}

.canvas--panel {
    display: flex;
    justify-content: center;
}

.btn{
    width: 70px;
    margin: 5px;
    font-size: 14px;
    font-weight: 100;
    height: 35px;
}
.button--half{
    max-width: 35px;
    height: 35px;
    padding: 0;
}
.subtitle{
    font-weight: 100;
    text-align: center;
    font-size: var(--titleSize2);
}

.subtitle--container {
    display: flex;
    justify-content: center;
}
.sandbox--container {
    /* position: relative; */
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.sandbox {
    position: relative;
    width: 300px;
    height: 300px;
    overflow: hidden;
}
.cell {
    position: absolute; 
    background-color: var(--ptColor);
    /* box-shadow: 0 0 2px 0px var(--ptColor); */
}


.filter {
    /* position: absolute; */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    --bur: 3px;
    -webkit-filter: blur(var(--bur));
    -moz-filter: blur(var(--bur));
    -o-filter:blur(var(--bur));
    -ms-filter:blur(var(--bur));
    filter: blur(var(--bur));
    z-index: 0;
}

.cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; 
    z-index: 10;
}

.description {
    padding-bottom: 70px;
}

.card-group {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.card {
    max-width: 200px;
    transition-duration: 0.2s;
    background: var(--bgColor2);
    border: 0;
    box-shadow: 0 0 10px 0px var(--txtColor3);
}
.card:hover {
    box-shadow: 0 0 10px 0px var(--txtColor1);
}

.card--image{
    margin: 5px;
    height: auto;
    width: auto;
    max-width: 200px;
}
.btn {
    width: 100px;
}

.preview--container{
    width: 300px;
    height: 300px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.9;
    background-color: var(--bgColor1);
}

.section-title {
    font-weight: 100;
    font-size: xx-large;
}

.section-paragraph {
    font-weight: 100;
}

.ref-url:hover {
    text-decoration: none;
}

.ref-btn {
    width: 150px;
}


.text-input {
    text-shadow: none;
    background-color: var(--bgColor1) !important;
    color: var(--txtColor1) !important;
}




.info--container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.info {
    padding: 20px 0px;
    font-size: 15px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
h1 {
    text-shadow: 0px 0px 10px var(--txtShadow1);
}

*, a {
    text-shadow: 0px 0px 10px var(--txtShadow1);
    color: var(--txtColor1);
    /* transition: 600ms ease; */
}

a:hover {
    text-decoration: none;
    color: var(--txtColor3);
}

#space-travel {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -3;
}

.btn {
    background-color: var(--bgColor3);
    color: var(--txtColor1);
    border: 0;
}

.btn:hover {
    background-color: var(--bgColor1);
    color: var(--txtColor3);
    box-shadow: 0 0 5px 1px var(--txtColor3);
}

.row {
    margin: 50px 0 0 0;
}

.col {
    width: 100%;
    flex-direction: column;
    justify-self: flex-start;
    justify-content: center;
    align-items: flex-start;
}

:root {
    --txtColor1: '#212121';
    --txtColor2: '#212121';
    --txtColor3: '#212121';
    --bgColor1: '#FAFAFA';
    --bgColor2: '#212121';
    --bgColor3: '#FAFAFA';
    --sdColor: '#FAFAFA';
    --txtShadow1: #f00;
    --txtShadow2: #f00;
    --txtShadow3: #f00;

    --titleSize1: 40px;
    --titleSize2: 30px;
    --titleSize3: 20px;
    --speed1: 500ms;
    --ptColor: #aaaa;
}
